<template>
  <div>
    <center style="padding-top: 33px; padding-bottom: 0px">
      <div class="container-x">
        <div class="countryDetail">
          <div class="searchCountry">
            <!-- <input type="text" v-model="searchQuery" /> -->

            <div class="autosuggest-container divv">
              Search:
              <vue-autosuggest
                :input-props="inputProps"
                style="z-index: 99"
                class="div2"
                :limit="10"
                v-model="searchQuery"
                :suggestions="filteredOptions"
                @focus="focusMe"
                @click="clickHandler"
                @input="onInputChange"
                @selected="onSelected"
                :get-suggestion-value="getSuggestionValue"
              >
                <div
                  slot-scope="{ suggestion }"
                  style="display: flex; align-items: center"
                >
                  {{ suggestion.item }}
                </div>
              </vue-autosuggest>
            </div>
          </div>
          <button
            v-if="showComparebtn && getCountry1.data.confirmed"
            @click="showLabel()"
          >
            Compare With
          </button>
          <label v-if="getCountry1.data.confirmed && showComparebtn == false"
            >Compare With</label
          >

          <div class="searchCountry" v-if="showComapareDiv">
            <!-- <input type="text" v-model="searchQuery2" /> -->
            <div class="autosuggest-container divv">
              <vue-autosuggest
                style="z-index: 80"
                class="comp"
                :limit="10"
                v-model="searchQuery2"
                :suggestions="filteredOptions2"
                @focus="focusMe2"
                @click="clickHandler2"
                @input="onInputChange2"
                @selected="onSelected2"
                :get-suggestion-value="getSuggestionValue2"
                :input-props="inputProps"
              >
                <div
                  slot-scope="{ suggestion }"
                  style="display: flex; align-items: center"
                >
                  <div style="{ display: 'flex', color: 'navyblue'}">
                    {{ suggestion.item }}
                  </div>
                </div>
              </vue-autosuggest>
            </div>
            <div class="compButton divv">
              <button class="buttoncpmpare" @click="closeCompare">X</button>
            </div>
          </div>
        </div>
      </div>

      <div class="linechart" v-if="this.chartData.length > 2">
        <center>
          <div class="main">
            <div
              class="sidenav"
              v-if="
                getCountry1.data.confirmed && this.getCountry2.data.confirmed
              "
            >
              <!-- <toggle-button
                class="toggleBtn2"
                style="visibility: hidden;"
              />-->
              <a
                href="#confirmed"
                @click="getClickedChart('confirmed')"
                id="totalcase"
                >Total Cases</a
              >
              <a href="#confirmed" id="firstcase">First Case</a>

              <a href="#deaths" @click="getClickedChart('deaths')">Deaths</a>
              <a href="#recovered" @click="getClickedChart('recovered')"
                >Recovered</a
              >
            </div>

            <div class="toggleComp">
              <toggle-button
                class="toggleBtn"
                @change="onChangeEventHandler"
                v-model="stateToggle"
              />
              <GChart
                type="LineChart"
                :data="chartData"
                :options="chartOptions"
                v-if="chartData.length > 2"
                class="comparechart"
              ></GChart>
            </div>
          </div>
        </center>
      </div>
      <div class="allData compAllData shadow" v-if="shadow1">
        <div class="compAllData">
          <table class="compData" v-show="getCountry1.data.confirmed">
            <tr>
              <td style="background-color: gray; font-weight: bold">Country</td>
              <td style="background-color: gray; font-weight: bold">
                {{ getCountry1.Country }}
              </td>
              <td
                style="background-color: gray; font-weight: bold"
                v-show="getCountry2.data.confirmed"
              >
                {{ getCountry2.Country }}
              </td>
            </tr>
            <tr>
              <td
                style="background-color: rgb(201, 192, 192); font-weight: bold"
              >
                TotalCases
              </td>
              <td style="background-color: rgb(201, 192, 192)">
                {{ getCountry1.data.confirmed }}
              </td>
              <td
                style="background-color: rgb(201, 192, 192)"
                v-show="getCountry2.data.confirmed"
              >
                {{ getCountry2.data.confirmed }}
              </td>
            </tr>
            <tr>
              <td
                style="background-color: rgb(201, 192, 192); font-weight: bold"
              >
                ActiveCases
              </td>
              <td style="background-color: rgb(201, 192, 192)">
                {{
                  getCountry1.data.confirmed -
                  (getCountry1.data.recovered + getCountry1.data.deaths)
                }}
              </td>
              <td
                style="background-color: rgb(201, 192, 192)"
                v-show="getCountry2.data.confirmed"
              >
                {{
                  getCountry2.data.confirmed -
                  (getCountry2.data.recovered + getCountry2.data.deaths)
                }}
              </td>
            </tr>

            <tr>
              <td
                style="background-color: rgb(201, 192, 192); font-weight: bold"
              >
                TotalRecovered
              </td>
              <td style="background-color: rgb(201, 192, 192)">
                {{ getCountry1.data.recovered }}
              </td>
              <td
                style="background-color: rgb(201, 192, 192)"
                v-show="getCountry2.data.confirmed"
              >
                {{ getCountry2.data.recovered }}
              </td>
            </tr>
            <tr>
              <td
                style="background-color: rgb(201, 192, 192); font-weight: bold"
              >
                TotalDeaths
              </td>
              <td style="background-color: rgb(201, 192, 192)">
                {{ getCountry1.data.deaths }}
              </td>
              <td
                style="background-color: rgb(201, 192, 192)"
                v-show="getCountry2.data.confirmed"
              >
                {{ getCountry2.data.deaths }}
              </td>
            </tr>
          </table>
        </div>
        <div class="compAllData" v-if="!getCountry2.data.confirmed">
          <GChart
            v-if="getCountry1.data.confirmed"
            style="margin-left: 10px"
            type="PieChart"
            :data="PiechartData"
            class="piehart"
          ></GChart>
        </div>
      </div>
    </center>
  </div>
</template>
<script>
import { GChart } from "vue-google-charts";
import { VueAutosuggest } from "vue-autosuggest";
import axios from "axios";
export default {
  name: "Countries",
  props: ["allDataProp"],
  components: {
    GChart,
    VueAutosuggest,
  },
  created() {
    axios
      .get("https://pomber.github.io/covid19/timeseries.json")
      .then((rsp) => {
        this.allDateSeries = rsp.data;
      });
  },
  data: function () {
    return {
      shadow1: false,
      allDateSeries: {},
      searchQuery: "",
      selected: "",
      showComparebtn: false,
      inputProps: {
        id: "autosuggest__input",
        onInputChange: this.onInputChange,
        placeholder: "Search Country",
      },
      showToggle: false,
      stateToggle: false,
      searchQuery2: "",
      selected2: "",
      countryname: [],
      getCountry1: { Country: "", data: {} },
      getCountry2: { Country: "", data: {} },
      showComapareDiv: false,
      chartData: [],
      PiechartData: [],
      chartOptions: {
        chart: {
          title: "",
        },
        legend: { position: "bottom" },
        width: 0,
        height: 0,
        hAxis: {
          format: "dd/MM/yy",
        },
      },
    };
  },
  computed: {
    filteredOptions() {
      return [
        {
          data: this.countryname.filter((option) => {
            return (
              option.toLowerCase().indexOf(this.searchQuery.toLowerCase()) > -1
            );
          }),
        },
      ];
    },
    filteredOptions2() {
      return [
        {
          data: this.countryname.filter((option) => {
            return (
              option.toLowerCase().indexOf(this.searchQuery2.toLowerCase()) > -1
            );
          }),
        },
      ];
    },
  },
  methods: {
    onChangeEventHandler(e) {
      // console.log(e)
      if (e.value) {
        document.getElementById("firstcase").style.display = "block";
        document.getElementById("firstcase").focus();
        document.getElementById("totalcase").style.display = "none";
        this.getClickedChart("firstcase");
      } else {
        document.getElementById("firstcase").style.display = "none";
        document.getElementById("totalcase").style.display = "block";
        document.getElementById("totalcase").focus();
        this.getClickedChart("confirmed");
      }
    },
    getCountryData() {
      if (!(this.searchQuery && this.searchQuery2)) {
        this.PiechartData = [];
        this.getCountry1.Country = this.searchQuery;
        this.getCountry1.data = this.allDateSeries[this.searchQuery][
          this.allDateSeries[this.searchQuery].length - 1
        ];

        this.PiechartData.push(["Country", "Covid19 Report"]);
        this.PiechartData.push([
          "Active Cases",
          this.getCountry1.data.confirmed,
        ]);
        this.PiechartData.push(["Recover", this.getCountry1.data.recovered]);
        this.PiechartData.push(["Deaths", this.getCountry1.data.deaths]);
        this.getCountry2.data = {};
        this.showComparebtn = true;
        this.shadow1 = true;
      }
    },
    showLabel() {
      this.showComparebtn = false;
      this.showComapareDiv = true;
    },
    getCompareData() {
      // this.chartData = [];
      this.showComapareDiv = true;
      if (!(this.searchQuery == "" || this.searchQuery2 == "")) {
        if (!(this.searchQuery == this.searchQuery2)) {
          this.getCountry1.Country = this.searchQuery;
          this.getCountry2.Country = this.searchQuery2;
          this.getCountry1.data = this.allDateSeries[this.searchQuery][
            this.allDateSeries[this.searchQuery].length - 1
          ];
          this.getCountry2.data = this.allDateSeries[this.searchQuery2][
            this.allDateSeries[this.searchQuery].length - 1
          ];
          // if (window.screen.availWidth > 700) {
          //   this.chartOptions.width = 0;
          //   this.chartOptions.height = 0;
          // }
          // if (window.screen.availWidth < 700) {
          //   this.chartOptions.width = 250;
          //   this.chartOptions.height = 200;
          // }
          this.chartData = [];
          this.chartOptions.title = "confirmed cases";
          this.chartData.push(["Date", this.searchQuery, this.searchQuery2]);
          for (
            var j = 0;
            j < this.allDateSeries[this.searchQuery].length;
            j++
          ) {
            this.chartData.push([
              new Date(this.allDateSeries[this.searchQuery][j].date),
              this.allDateSeries[this.searchQuery][j]["confirmed"],
              this.allDateSeries[this.searchQuery2][j]["confirmed"],
            ]);
          }
        } else {
          alert("Please Enter Different Country Name To Comaprison");
        }
      } else {
        alert("enter correct details");
      }
    },
    closeCompare() {
      // this.getCountry1={};
      this.shadow1 = false;
      this.shadow2 = false;
      this.getCountry2.data = {};
      this.chartData = {};
      this.showComapareDiv = false;
      this.searchQuery2 = "";
      this.getCountry1.data = {};
      this.searchQuery = "";
    },
    getClickedChart(chartType) {
      this.chartOptions.title =
        chartType[0].toUpperCase() + chartType.slice(1) + " " + "Cases";
      // if (window.screen.availWidth > 700) {
      //   this.chartOptions.width = 0;
      //   this.chartOptions.height = 0;
      // }
      // if (window.screen.availWidth < 700) {
      //   this.chartOptions.width = 250;
      //   this.chartOptions.height = 200;
      // }
      // console.log(chartType);
      if (chartType == "confirmed") {
        // this.showToggle = true;
        var x = document.getElementsByClassName("toggleBtn");
        x[0].style.visibility = "visible";
        this.stateToggle = false;
        this.showToggle = true;
      } else if (chartType == "firstcase") {
        x = document.getElementsByClassName("toggleBtn");
        x[0].style.visibility = "visible";
        this.stateToggle = true;
        this.showToggle = true;
      } else {
        this.stateToggle = false;
        x = document.getElementsByClassName("toggleBtn");
        x[0].style.visibility = "hidden";
        this.showToggle = false;
        document.getElementById("firstcase").style.display = "none";
        document.getElementById("totalcase").style.display = "block";
      }
      this.chartData = [];
      if (chartType == "firstcase") {
        this.chartOptions.hAxis.format = "";
        this.chartData.push(["Day", this.searchQuery, this.searchQuery2]);
        var k, len1, arr1, arr2;
        for (j = 0; j < this.allDateSeries[this.searchQuery].length; j++) {
          if (this.allDateSeries[this.searchQuery][j].confirmed > 0) {
            arr1 = this.allDateSeries[this.searchQuery].slice(j);
            break;
          }
        }
        for (k = 0; k < this.allDateSeries[this.searchQuery2].length; k++) {
          if (this.allDateSeries[this.searchQuery2][k].confirmed > 0) {
            arr2 = this.allDateSeries[this.searchQuery2].slice(k);
            break;
          }
        }
        if (k > j) {
          console.log("j is smaller");
          len1 = this.allDateSeries[this.searchQuery].length - k;
        }
        if (j >= k) {
          console.log("k is smaller");
          len1 = this.allDateSeries[this.searchQuery2].length - j;
        }
        for (var m = 0; m < len1; m++) {
          this.chartData.push([m, arr1[m].confirmed, arr2[m].confirmed]);
        }
        // console.log(j, k);
        // console.log(len1);
        // console.log(arr1,arr2);
        // console.log(this.chartData);
        //        for (j = 0; j < this.allDateSeries[this.searchQuery].length; j++) {
        //   for (
        //     var k = 0;
        //     k < this.allDateSeries[this.searchQuery2].length;
        //     k++
        //   ) {
        //     if (
        //       this.allDateSeries[this.searchQuery][j].confirmed > 0 ||
        //       this.allDateSeries[this.searchQuery2][k].confirmed > 0
        //     ) {
        //       if (j > k) {
        //         this.chartData.push([
        //           new Date(this.allDateSeries[this.searchQuery][j].date),
        //           this.allDateSeries[this.searchQuery][j]["confirmed"],
        //           0
        //         ]);
        //       }
        //       if ((j = k)) {
        //         this.chartData.push([
        //           new Date(this.allDateSeries[this.searchQuery][j].date),
        //           this.allDateSeries[this.searchQuery][j]["confirmed"],
        //           this.allDateSeries[this.searchQuery2][j]["confirmed"]
        //         ]);
        //       }
        //       if (k > j) {
        //         this.chartData.push([
        //           new Date(this.allDateSeries[this.searchQuery2][k].date),
        //           0,
        //           this.allDateSeries[this.searchQuery2][j]["confirmed"]
        //         ]);
        //       }
        //     }
        //   }
        // }
      } else {
        this.stateToggle = false;
        this.chartData.push(["Date", this.searchQuery, this.searchQuery2]);
        for (var j = 0; j < this.allDateSeries[this.searchQuery].length; j++) {
          this.chartData.push([
            new Date(this.allDateSeries[this.searchQuery][j].date),
            this.allDateSeries[this.searchQuery][j][chartType],
            this.allDateSeries[this.searchQuery2][j][chartType],
          ]);
        }
      }
    },
    clickHandler() {
      // console.log(item);
    },
    onSelected(item) {
      this.selected = item.item;
      this.searchQuery = item.item;
      this.getCountryData();
    },
    onInputChange() {},
    getSuggestionValue(suggestion) {
      return suggestion.item;
    },
    focusMe() {
      // console.log(e); // FocusEvent
    },
    clickHandler2() {
      // console.log(item);
    },
    onSelected2(item) {
      this.selected2 = item.item;
      this.searchQuery2 = item.item;
      this.getCompareData();
    },
    onInputChange2() {
      // event fired when the input changes
      // console.log(text);
    },
    /**
     * This is what the <input/> value is set to when you are selecting a suggestion.
     */
    getSuggestionValue2(suggestion) {
      return suggestion.item;
    },
    focusMe2() {
      // console.log(e); // FocusEvent
    },
  },
  watch: {
    allDateSeries: function () {
      this.countryname = Object.keys(this.allDateSeries);
    },
  },
};
</script>
