<template>
  <center>
    <div class="home">
      <div id="map">
        <div class="div1">World Map</div>
        <geoChart :alldataprop="allDataProp" class="worldMap"></geoChart>
      </div>
      <div id="datachart">
        <div class="worldData">
          <TotalData
            title="Total Case"
            :titleValue="totalCase"
            class="totaldata"
          />
          <TotalData
            title="Total Deaths"
            :titleValue="deaths"
            class="totaldata"
          />
          <TotalData
            title="Total Recovered"
            :titleValue="recovered"
            class="totaldata"
          />
        </div>
        <h1 v-if="recovered">Covid-19 Coronavirus Cases</h1>
      </div>
      <div id="pie">
        <pieChart :covidDataProps="pieChartData" class="worldPie" />
        <h1 v-if="pieChartData.length > 2">Covid-19 Detail On Piechart</h1>
      </div>
    </div>
  </center>
</template>

<script>
// @ is an alias to /src
import TotalData from "@/components/TotalData.vue";
import geoChart from "@/components/geoChart.vue";
import Axios from "axios";
import pieChart from "@/components/pieChart.vue";

export default {
  name: "Home",
  props: {
    allDataProp: Object,
  },
  components: {
    TotalData,
    geoChart,
    pieChart,
  },
  data() {
    return {
      covid_url: "https://pomber.github.io/covid19/timeseries.json",
      pieChartData: {},
      show: false,
      totalCase: "",
      deaths: "",
      recovered: "",
      allData: {},
    };
  },
  created() {
    Axios.get("https://api.covid19api.com/summary").then((rsp) => {
      let data = rsp.data;
      this.totalCase = data.Global.TotalConfirmed;
      this.deaths = data.Global.TotalDeaths;
      this.recovered = data.Global.TotalRecovered;
      let activeCases =
        parseInt(this.totalCase) -
        parseInt(this.deaths) -
        parseInt(this.recovered);
      this.pieChartData = [
        ["Type", "Cases"],
        ["Active Cases", activeCases],
        ["Deaths", this.deaths],
        ["Recovered", this.recovered],
      ];
    });
  },
};
</script>

