<template>
  <div id="geochart" v-if="columns.length">
    <center>
      <vue-chart
        chart-type="GeoChart"
        :columns="columns"
        :rows="rows"
        :options="chartOptions"
      ></vue-chart>
    </center>
  </div>
</template>

<script>
export default {
  props: {
    alldataprop: Object,
  },
  created() {
    for (const key in this.alldataprop) {
      const length = this.alldataprop[key].length - 1;
      this.rows.push([
        { v: key, f: key },
        parseInt(this.alldataprop[key][length - 1].confirmed),
        parseInt(this.alldataprop[key][length - 1].deaths),
      ]);
    }
  },
  data() {
    return {
      columns: [
        {
          type: "string",
          label: "Country",
        },
        {
          type: "number",
          label: "Confirmed",
        },
        {
          type: "number",
          label: "Deaths",
        },
      ],
      rows: [],
      chartData: [[{ v: "", f: "" }, "Cases", "Deaths"]],
      chartOptions: {
        colorAxis: {
          values: [1000, 10000, 100000, 1000000],
          colors: ["green", "#D1E231", "orange", "red"],
        },
        width: 1000,
        height: 500,
        region: "world",
        sizeAxis: { minValue: 0, maxValue: 1000000 },

        backgroundColor: "#81d4fa",
        datalessRegionColor: "#999",
        defaultColor: "#98fb98",
      },
    };
  },
};
</script>

<style scoped>
#gchart {
  width: 100%;
  height: 500px;
}
@media only screen and (min-width: 650px) and (max-width: 800px) {
  #gchart {
    width: 100%;
    height: 500px;
  }
}
@media only screen and (min-width: 500px) and (max-width: 650px) {
  #gchart {
    width: 100%;
    height: 500px;
  }
}
@media only screen and (min-width: 400px) and (max-width: 500px) {
  #gchart {
    width: 100%;
    height: 500px;
  }
}
@media only screen and (min-width: 250px) and (max-width: 400px) {
  #gchart {
    width: 100%;
    height: 500px;
  }
}
</style>
