import Vue from 'vue'
import VueRouter from 'vue-router'
import Countries from '../views/Countries.vue'
import Home from '../views/Home.vue'
import Table from '../views/TableData.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/countries',
    name: 'Countries',
    component: Countries
  },
  {
    path: '/table',
    name: 'Table',
    component: Table
  }
]

const router = new VueRouter({
  routes
})

export default router
