<template>
  <div id="pieChart">
    <GChart
      type="PieChart"
      :data="covidDataProps"
      :options="chartOptions"
      v-if="covidDataProps.length > 2"
      id="piechart"
    />
  </div>
</template>

<script>
import { GChart } from "vue-google-charts";

export default {
  props: {
    covidDataProps: [Object, Array],
  },
  created() {},
  data() {
    return {
      chartOptions: {
        chart: {
          height: 500,
          title: "World Data",
          subtitle: "World Data in Pie Chart",
        },
      },
    };
  },
  components: {
    GChart,
  },
};
</script>

<style scoped>
#piechart {
  width: 100%;
  height: 300px;
}
