<template>
  <div class="container-x">
    <div class="row">
      <div class="tableDiv">
        <input v-model="search" placeholder="Search" />
        <table class="table table-hover" v-if="this.filtered">
          <thead class="tableHead">
            <tr>
              <th scope="col" style="left: 0; z-index: 20">Country</th>
              <th scope="col">Total Cases</th>
              <th scope="col">New Cases</th>
              <th scope="col">Total Deaths</th>
              <th scope="col">New Deaths</th>
              <th scope="col">Total Recovered</th>
            </tr>
          </thead>
          <tbody class="tableBody">
            <tr v-for="i in this.filtered" :key="i.ID" class="table-row">
              <th>{{ i.Country }}</th>
              <td>{{ i.TotalConfirmed }}</td>
              <td class="text-danger">{{ i.NewConfirmed }}</td>
              <td>{{ i.TotalDeaths }}</td>
              <td class="text-danger">{{ i.NewDeaths }}</td>
              <td>
                {{ i.TotalRecovered == "N/A" ? "--" : i.TotalRecovered }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
import Axios from "axios";
export default {
  name: "TableData",
  prop: {
    allDataProp: {
      type: Object,
    },
  },
  data() {
    return {
      allData: [],
      search: "",
    };
  },
  computed: {
    filtered: function () {
      return this.allData.filter((blog) => {
        return blog.Country.toLowerCase().match(this.search.toLowerCase());
      });
    },
  },
  created() {
    Axios.get("https://api.covid19api.com/summary").then((rsp) => {
      this.allData = rsp.data.Countries;
    });
  },
};
</script>