<script>
import axios from "axios";

export default {
  name: "App",

  created() {
    axios
      .get("https://pomber.github.io/covid19/timeseries.json")
      .then((rsp) => {
        this.allData = rsp.data;
      });
  },
  data() {
    return {
      allData: {},
    };
  },
};
